.bannerQr{width:100%;height:90vh;background-image:url("../../img/jpg/pizza.jpg");background-size:cover;background-repeat:no-repeat;background-position:center}.bannerQr .container{position:absolute;top:50%;left:50%;color:#fff;transform:translate(-50%, -50%);overflow:hidden;text-align:center;font-weight:700;text-transform:uppercase}.bannerQr .container h1{font-family:"Merienda One",cursive;letter-spacing:2px;margin-top:6px}.bannerQr .container h1 span{border:10px solid #fff;padding:10px 14px;display:inline-block}.bannerQr .container .des{margin:20px;display:block;font-size:26px;text-shadow:0 0 10px #000;font-family:"Dancing Script",cursive;letter-spacing:2px}.bannerQr .container .des:before{content:"";animation:animate1 infinite 12s}@keyframes animate1{0%{content:"Menú digital seguro e higiénico"}50%{content:"Totalmente escalable e interactivo"}100%{content:"Evita contagios por contactos"}}.bannerQr .container a{color:#313131;padding:10px 24px;text-decoration:none;background:#f1f1f1;border-radius:8px;font-size:20px;transition:.5s all;font-weight:bold;font-family:"Jura",sans-serif}.bannerQr .container a:hover{background:#bcbe2e}